.sprite-sprite-tata-white-circle,
.sprite-sprite-tata-no-bg,
.sprite-sprite-tata-light-blue,
.sprite-sprite-tata-other-side {
  background-image: url('http://imgcdn.tataufo.com/sprite/sprite.png');
  background-repeat: no-repeat;
}

.sprite-sprite-tata-with-tile-white-horizontal {
  background-position: 0 0;
  width: 154px;
  height: 47px;
}

.sprite-sprite-tata-with-tile-blue-horizontal {
  background-position: 0 -47px;
  width: 154px;
  height: 47px;
}

.sprite-sprite-tata-title-zh-white {
  background-position: 0 -94px;
  width: 92px;
  height: 28px;
}

