}

.sprite-sprite-tata-title-white {
  background-position: -154px 0;
  width: 92px;
  height: 16px;
}

.sprite-sprite-tata-with-title {
  background-position: -154px -16px;
  width: 88px;
  height: 76px;
}

.sprite-sprite-tata-title-blue {
  background-position: -154px -92px;
  width: 88px;
  height: 16px;
}

.sprite-sprite-tata-icon {
  background-position: 0 -149px;
  width: 56px;
  height: 53px;
}

.sprite-sprite-tata-white-circle {
  background-position: -56px -149px;
  width: 50px;
  height: 48px;
}

.sprite-sprite-tata-no-bg {
  background-position: -154px -107px;
  width: 50px;
  height: 40px;
}

.sprite-sprite-tata-light-blue {
